@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --inputColor : #0195F7;
  /* --inputColor : #0195F7; */

}

*{
  padding: 0;
  margin: 0;
  background-color: #FAFAFA;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

nav{
  position: fixed;
  background: #0195F7;
}
nav .Riddim{
  font-family: "Poppins",sans-serif;
    font-size: 30px;
    padding: 10px 0 0 20px;
}

.container{
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  
  margin: auto;
}

.Riddim-Ai{
    font-family: "Poppins",sans-serif;
    font-size: 50px;

}
.output{
  color: #121212;
  width: 30rem;
  margin-bottom: 4rem;
}

.input{
  width: 30rem;
  
}


input{
  width: 100%;
  height: 32px;
  padding: 4px 4px 16px;
  font-size: 28px;
  box-sizing: border-box;
  font-weight: 450;
  border: none;
  color: #0195F7;
  color: var(--inputColor);
  border-bottom: 1px solid #0195F7;
  border-bottom: 1px solid var(--inputColor);
  outline: none;
}

.input :focus{
  border-bottom: 2px solid #0195F7;
  border-bottom: 2px solid var(--inputColor);

}

p{
  color: #121212;
  font-size: 20px;
}

.musicplayer{
  /* display: none; */
  width: 80%;
  margin:4rem auto 0 auto;
  font-family: "Poppins" , sans-serif;
}

.mood{
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  font-size: medium;
  color: #0195F7;
  color: var(--inputColor);
}
span{
  display: block;
  text-align: end;
  color: rgb(104, 104, 104);
  margin-top: 10px;
  font-size: small;
}
a{
  color: #0195F7;
  color: var(--inputColor);
  text-decoration: none;
  font-weight: 500;
}

::-webkit-input-placeholder{
  color:  #0195F7;
  color:  var(--inputColor);
  opacity: 0.6;
  font-weight: 100;

}

:-ms-input-placeholder{
  color:  #0195F7;
  color:  var(--inputColor);
  opacity: 0.6;
  font-weight: 100;

}

::placeholder{
  color:  #0195F7;
  color:  var(--inputColor);
  opacity: 0.6;
  font-weight: 100;

}


.musicplayer_{
    background-color: #f1f1f1;
    padding: 10px ;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 80%;
    height: 4rem;
    /* border: #121212 solid 2px; */
    border-radius: 8px;
    margin: auto;
    border-radius: 10px;
  box-shadow: 0 1px 0 rgba(0,0,0,.15);
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.musicplayer_:hover{
    box-shadow: 1px 1px 1px #000000;
    /* border: #0195F7  solid 1px; */
    
}

.disc{
    margin: 10px;
    border-radius: 100%;
    height: 3rem;
    width: 3rem;
    border: #121212 solid 4px;
    background: url(/riddim-ai/static/media/disc.0f99fb74.png);
    background-size: cover;

}
.display{
    background-color: #f1f1f1;
    display: grid;
    grid-template-columns: auto;
}
.display .heading{
    background-color: #f1f1f1;
    font-weight: 600;
    font-size: medium;
}
.display .composer{
    background-color: #f1f1f1;
    color: #a5a5a5;
    font-size: small;
}


.image {

    -webkit-animation:spin 4s linear infinite;
    animation:spin 4s linear infinite;
}
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.blink{
    color: red;
    pointer-events: none;
    -webkit-animation: blink 1s linear infinite;
            animation: blink 1s linear infinite;
}

@-webkit-keyframes blink{
    0%{opacity: 0;}
    50%{opacity: .5;}
    100%{opacity: 1;}
    }

@keyframes blink{
    0%{opacity: 0;}
    50%{opacity: .5;}
    100%{opacity: 1;}
    }

