@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

:root{
  --inputColor : #0195F7;
  /* --inputColor : #0195F7; */

}

*{
  padding: 0;
  margin: 0;
  background-color: #FAFAFA;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

nav{
  position: fixed;
  background: #0195F7;
}
nav .Riddim{
  font-family: "Poppins",sans-serif;
    font-size: 30px;
    padding: 10px 0 0 20px;
}

.container{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  
  margin: auto;
}

.Riddim-Ai{
    font-family: "Poppins",sans-serif;
    font-size: 50px;

}
.output{
  color: #121212;
  width: 30rem;
  margin-bottom: 4rem;
}

.input{
  width: 30rem;
  
}


input{
  width: 100%;
  height: 32px;
  padding: 4px 4px 16px;
  font-size: 28px;
  box-sizing: border-box;
  font-weight: 450;
  border: none;
  color: var(--inputColor);
  border-bottom: 1px solid var(--inputColor);
  outline: none;
}

.input :focus{
  border-bottom: 2px solid var(--inputColor);

}

p{
  color: #121212;
  font-size: 20px;
}

.musicplayer{
  /* display: none; */
  width: 80%;
  margin:4rem auto 0 auto;
  font-family: "Poppins" , sans-serif;
}

.mood{
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  font-size: medium;
  color: var(--inputColor);
}
span{
  display: block;
  text-align: end;
  color: rgb(104, 104, 104);
  margin-top: 10px;
  font-size: small;
}
a{
  color: var(--inputColor);
  text-decoration: none;
  font-weight: 500;
}

::placeholder{
  color:  var(--inputColor);
  opacity: 0.6;
  font-weight: 100;

}